import { FormattedMessage } from 'react-intl';

// material-ui
import {
    TableCell,
    TableHead,
    TableRow,
    Typography
} from '@mui/material';

// project imports
// import Chip from 'ui-component/extended/Chip';

// assets

import {       HeadCell
} from 'types';


export interface OrderListEnhancedTableHeadProps  {
    headers:any;
    action?:boolean
    small?:boolean
}

export function EnhancedTableHead(props: OrderListEnhancedTableHeadProps) {
    const {   headers,    small, action } = props;

    return (
        <TableHead 
        // style={small?{height:'122px !important',   color:'white', padding:221}:{}}

        
        >
            <TableRow 

            
            >
               
                  {  headers.map((headCell:HeadCell) => (
                        <TableCell
                        style={small?{height:'0rem',textOverflow:'clip',  paddingTop:0,paddingBottom:0}:{}}


                            key={headCell.id}
                            align={headCell.align}
                            padding={headCell.disablePadding ? 'none' : 'normal'}
                           
                        >
                          
                                {headCell.label}
                     
                     
                        </TableCell>
                    ))}
           {    action&&     <TableCell sortDirection={false} align="center" sx={{ pr: 1}}>
                        <Typography variant="subtitle1" sx={{ color:  'grey.900' }}>
                        <FormattedMessage id="action" />

                        </Typography>
                    </TableCell>}
            </TableRow>
        </TableHead>
    );
}