import * as React from 'react';
import { Grid, Divider, Table, TableBody, TableCell, TableContainer, TableRow, Chip } from '@mui/material';

import MainCard from 'ui-component/cards/SubCard';
import { EnhancedTableHead } from './InnerTableHeader';
import TablePagination1 from '../TablePagination';

import {
    attacks_fields,
    DefaultRootStateProps,
    HeadCell,
    headCellsAttack,
    NavigationEnum,
    flows_fields,
    headCellsFlow,
    headCellsInnerFlow,
    inner_flows_fields
} from 'types';
import CustomizedTreeView from 'views/secunity-ui/component/CustomizedTreeView';
import useAuth from 'common/hooks/useAuth';

import { useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';

import { pydict } from 'utils/transformation/pydict';
import { TableActionCell } from './TableActionCell';
// import { matrieal_icons } from 'common/menu-items/application';

export type OrderData = any;

interface PropsRowTable {
    cells: string[];
    model: NavigationEnum;
    row_obj: any;
    index: number;
    action:boolean
    // setDialog_delete: Function;
    // setOpen: Function;
    // model_name: string;
}
interface PropsWrapTable {
    children: React.ReactNode;
    header?: any;
}
export const WrapTable = ({ children, header }: PropsWrapTable) => {
    return (
        <TableContainer>
            <Table sx={{ minWidth: 750 }} aria-labelledby="tableTitle">
                {/* <div style={{zIndex:-2}}> */}

                {/* </div> */}
                <TableBody>
                    {/* <TableRow hover role="checkbox" aria-checked tabIndex={-1} key={Math.random().toString()}> */}
                    {children}
                    {/* </TableRow> */}
                </TableBody>

                {header}
            </Table>
        </TableContainer>
    );
};

export const RowTable = ({ cells, model, row_obj, index, action }: PropsRowTable) => {
    return (
        <>
            <TableRow hover role="checkbox" aria-checked tabIndex={-1} key={index}>
                {cells.map((cell: string) => (
                    <TableCell align="center" sx={{ p: 0.5 }}>
                        {cell !== 'tcp_flags' ? (
                            pydict(row_obj, cell, ' ')
                        ) : (
                            <Grid display="flex" direction="row" alignItems="center" justifyContent="center" alignContent="center">
                                {pydict(row_obj, cell, []).map((flag: string) => (
                                    <Chip
                                        label={flag}
                                        color="primary"
                                        size="small"
                                        sx={
                                            {
                                                // ml: 1
                                                // ml: .31, mt: 1.4
                                            }
                                        }
                                    />
                                ))}
                            </Grid>
                        )}
                    </TableCell>
                ))}

               {action&& <TableActionCell obj={row_obj} model={model} />}
            </TableRow>
        </>
    );
};
interface Props {
    cells: string[];
    model: NavigationEnum;
    headCells: HeadCell[];
    rowsPerPage:number;
setRowsPerPage:Function;
}
export const TableListAction = ({ cells, model, headCells, rowsPerPage ,setRowsPerPage }: Props) => {
    const params = useParams();
    const { model_name } = params;

    const [page, setPage] = React.useState<number>(0);

    const tableData = pydict(
        useSelector((state: DefaultRootStateProps) => state.models),
        model,
        {}
    );
    const { permissions } = useAuth();

    const [rows, setRows] = React.useState<OrderData[]>(tableData.entities || []);
    React.useEffect(() => {
        setRows(tableData.entities || []);

        // set_models(model_name as string, body);
    }, [model_name, tableData]);

    return (
        <MainCard
            contentSX={{ m: -1.5, p: -1.5 }}
            sx={{ textAlign: 'center', m: -0.5, p: -0.5 }}
            title={model.toUpperCase().split('_').join(' ')}
            content={false}
        >
            {/* <WrapTable> */}
            {rows.map((row, index) => {
                const attack_comp = (
                    <WrapTable header={<EnhancedTableHead headers={headCells} action={permissions.can_edit_context_account}  small />}>
                        <RowTable action={permissions.can_edit_context_account}  cells={cells} model={model} row_obj={row.detail} index={index} />
                    </WrapTable>
                );
                return row.flows && row.flows.length > 0 ? (
                    /* <CustomizedTreeView label_comp={  <TableCell>sds</TableCell>                   }/> */
                    <>
                        <CustomizedTreeView
                            main={attack_comp}
                            label_comp={
                                row.flows &&
                                row.flows.length > 0 && (
                                    <WrapTable header={<EnhancedTableHead headers={headCellsInnerFlow} action={permissions.can_edit_context_account} />}>
                                        {row.flows.map((flow_row: any, index2: number) => (
                                            <RowTable
                                                cells={inner_flows_fields}
                                                model={model}
                                                row_obj={flow_row}
                                                index={(index2 + 1) * (index + 1)}
                                                action={permissions.can_edit_context_account} 
                                            />
                                        ))}
                                    </WrapTable>
                                )
                            }
                        />
                    </>
                ) : (
                    attack_comp
                );
            })}

            <TablePagination1
                model={model_name}
                pagination_model
                rows={tableData.total}
                page={page}
                setPage={setPage}
                rowsPerPage={rowsPerPage}
                setRowsPerPage={setRowsPerPage}
            />
        </MainCard>
    );
};

export const TableFlows = ({rowsPerPage ,setRowsPerPage, model}:any) => {
    // const { cells, headCells, body } = get_table_data(model_name);
    const cells = [attacks_fields, flows_fields];
    const headCells = [headCellsAttack, headCellsFlow];
    
    return (
        <Grid>
            {
            // [NavigationEnum.ATTACKS_TABLE].map((model: NavigationEnum, index: number) => (
                <>
                    <MainCard>
                        <TableListAction   rowsPerPage={rowsPerPage}
                setRowsPerPage={setRowsPerPage} cells={cells[0]} model={model} headCells={headCells[0]} />
                    </MainCard>
                    <Divider sx={{ my: 1.5 }} />
                </>
            // ))
            }
        </Grid>
    );
};

