import * as React from 'react';

import { TablePagination } from '@mui/material';
import { useActions } from 'store/actions';
import { get_table_data } from '.';
import useApi from 'common/hooks/useApi';
import HocNavigate from 'common/hooks/useNavigate';

interface Pagination {
    rows: number;
    page: any;
    setPage: any;

    
    rowsPerPage: any;
    setRowsPerPage: any;
    pagination_model: boolean;
    model?: string;
}
const TablePagination1 = ({ rows, page, setPage, rowsPerPage, setRowsPerPage, pagination_model, model }: Pagination) => {
    const { set_models } = useActions();
    const navigate = HocNavigate();

    const body: any = model ? get_table_data(model) : {};
    const getPostsApi = useApi(set_models);

    const handleChangePage = (event: React.MouseEvent<HTMLButtonElement, MouseEvent> | null, newPage: number) => {
        setPage(newPage);
        model && getPostsApi.request(model, { page_limit: rowsPerPage, page_offset: newPage + 1, ...body.body });
        model && navigate.request_navigate(`table/${model}/${newPage + 1}`);
    };

    const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement> | undefined) => {
        const page_limit = parseInt(event?.target.value as string, 10);
        page_limit && setRowsPerPage(page_limit);
        setPage(0);
        model && getPostsApi.request(model, { page_limit, page_offset: 1, ...body.body });
        model && navigate.request_navigate(`table/${model}/${1}`);
    };
    const rows_per_page = pagination_model
        ? {
              rowsPerPageOptions: [5, 15, 25],
              component: 'div'
          }
        : {
              rowsPerPageOptions: []
          };

    return (
        <TablePagination
            // rowsPerPageOptions={[5, 10, 25]}
            // component="div"

            {...rows_per_page}
            count={rows}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
        />
    );
};

export default TablePagination1;
