
import {
    account_fields, accounts_users_fields, networks_fields, devices_fields, HeadCell,attacks_fields,
    headCellsAccount, headCellsAccountUser, headCellsNetwork, headCellsDevice,  headCellsAttack, NavigationEnum, blackhole_fields, headCellsBlackhole, FLOW_ACTION, 

} from 'types';

export type OrderData = any;
type TableData = {
    cells: string[];
    headCells: HeadCell[];
    body:{}

}


export const get_action = (model_name: string, row:any={}):FLOW_ACTION  => {
    let action = model_name!=='cooldown'?FLOW_ACTION.REMOVE:row['status']!=='awaiting_moderated_approval'?FLOW_ACTION.APPLY:FLOW_ACTION.APPROVE
    switch (model_name) {
        case 'cooldown':
            action = FLOW_ACTION.REMOVE
            // body['options'] = [ "extended", "client_flowspec_ip"]

            break
        case 'active':
            action = FLOW_ACTION.REMOVE

            break
        case NavigationEnum.BLACKHOLE:
            action = row['status']!=='awaiting_moderated_approval' ?FLOW_ACTION.REMOVE:FLOW_ACTION.REAPPLY
            
            break

    }
    return action
}
export const get_table_data = (model_name: string): TableData => {
    let cells: string[] = account_fields;
    let headCells: HeadCell[] = headCellsAccount;
    const body = {"options": [ "general"]}
    switch (model_name) {
        case NavigationEnum.ACCOUNTS_TABLE:
            // inside_model = 'accounts'
            headCells = headCellsAccount
            cells = account_fields
            body['options'] = [ "general", "inventory"]
            break

        case NavigationEnum.ACCOUNT_USERS_TABLE:
            cells = accounts_users_fields
            headCells = headCellsAccountUser
            break
        case NavigationEnum.NETWORKS_TABLE:
            cells = networks_fields
            headCells = headCellsNetwork
            body['options'] = [ "general", "address_as_cidr", 'holistic_as_bool', 'rate_limit_as_bool']

            break
        case NavigationEnum.DEVICES_TABLE:
            cells = devices_fields
            headCells = headCellsDevice
            body['options'] = [ "extended", "client_flowspec_ip", "controller_xflow_ip"]
            // body['options'] = [ "extended", "client_flowspec_ip"]

            break
        case 'cooldown':
            cells = attacks_fields
            headCells = headCellsAttack
            body['options'] = [ "extended", "general"]
            // body['options'] = [ "extended", "client_flowspec_ip"]

            break
        case 'active':
            cells = attacks_fields
            headCells = headCellsAttack
            body['options'] = [ "extended", "general"]
            // body['options'] = [ "extended", "client_flowspec_ip"]

            break
        case NavigationEnum.BLACKHOLE:
            cells = blackhole_fields
            headCells = headCellsBlackhole
            body['options'] = [ "device_name"]
            // body['options'] = [ "extended", "client_flowspec_ip"]

            break

    }
    return { cells, headCells, body }
}

