import * as React from 'react';
import { FormattedMessage } from 'react-intl';
import { Button,  TableCell } from '@mui/material';


import {   NavigationEnum,   FLOW_ACTION } from 'types';

import { useParams } from 'react-router-dom';

import { useActions } from 'store/actions';
import DraggableDialog from 'ui-component/extended/DraggableDialog';
import useApi from 'common/hooks/useApi';
// import { matrieal_icons } from 'common/menu-items/application';


interface Props {
    obj: any;
    model: NavigationEnum;
}
export const TableActionCell = ({ obj, model }: Props) => {
    const params = useParams();
    const { model_name } = params;
    const { flow_action } = useActions();
    

    // const isSelected = (name: string) => selected.indexOf(name) !== -1;

    const [open, setOpen] = React.useState(false);
    const status = obj['status']
    let button:FLOW_ACTION;
     if( status===FLOW_ACTION.APPLIED)
     button = FLOW_ACTION.REMOVE
     else if ( status==='removed')

     button= FLOW_ACTION.REAPPLY
     else if(model_name ==='cooldown')
     button = FLOW_ACTION.APPLY
     else if(model_name ==='active'  && status !== 'awaiting_moderated_approval')
     button = FLOW_ACTION.REMOVE

     else 
     button = FLOW_ACTION.APPROVE
     const getPostsApi =   useApi(flow_action)

     
    const [dialog_delete, setDialog_delete] = React.useState<any>(null);
    const send_delete = (object_id: string, row: any) => {
        let action:string =
            model_name !== 'cooldown' && row['status'] !== 'awaiting_moderated_approval'
                ? FLOW_ACTION.REMOVE
                : row['status'] !== 'awaiting_moderated_approval'
                ? FLOW_ACTION.APPLY
                : FLOW_ACTION.APPROVE;

        if (     button === FLOW_ACTION.REAPPLY)
        action='re-apply'
        if(model === NavigationEnum.BLACKHOLE)
        action =status ===FLOW_ACTION.APPLIED? FLOW_ACTION.REMOVE:FLOW_ACTION.REAPPLY
        // const a = FLOW_ACTION.REAPPLY
        const path = model === NavigationEnum.BLACKHOLE ? model : 'flows'

        // flow_action();
     getPostsApi.request(path, object_id, action, model_name as string);

    };
    React.useEffect(() => {
       !open && setDialog_delete(null);
    }, [open]);


     
    return (
        <TableCell  
        align="center"
        onClick={(event:any)=>{
            event.stopPropagation();
            event.preventDefault();
        }}
        sx={{ p: 0, m: 0 }}>
            {dialog_delete}
{        button &&    <Button
                variant="contained"
                color="warning"
                sx={{ borderRadius: '1rem',height: 17, m:.5 }}
                onClick={() => {
                    setOpen(true);
                    setDialog_delete(
                        <DraggableDialog
                            title={`${button} Flow`}
                            open
                            func_send={send_delete}
                            args={[obj['id'], obj]}
                            setOpen={setOpen}
                        />
                    );
                }}
            >
                <FormattedMessage
                    id={
                        button
                    }
                />
            </Button>}
        </TableCell>
    );
};

