import { IconButton, TableCell, TableRow, Grid, Typography } from '@mui/material';
import { TableActionCell } from '../table_flows/TableActionCell';
import { matrieal_icons } from 'common/menu-items/application';
import HocNavigate from 'common/hooks/useNavigate';
import { ACCOUNT_TYPE, NavigationEnum } from 'types';
import useAuth from 'common/hooks/useAuth';
import { pydict } from 'utils';
import { FormattedMessage } from 'react-intl';
import { Link } from 'react-router-dom';

const customColumnStyle = {
    minWidth: '4rem'
};

interface PropsRowTable {
    cells: string[];
    model: NavigationEnum;
    row_obj: any;
    index: number;
    dialog_delete: Function;
    open_modal: Function;
}
const RowTable = ({ cells, model, row_obj, index, dialog_delete, open_modal }: PropsRowTable) => {
    const getPostsApi = HocNavigate();
    const { permissions } = useAuth();
    const is_isp = [ACCOUNT_TYPE.ISP, ACCOUNT_TYPE.ROOT].includes(permissions.type_account) ;
    const show_account = is_isp&& [ NavigationEnum.NETWORKS_TABLE, NavigationEnum.DEVICES_TABLE, NavigationEnum.ACCOUNT_USERS_TABLE].includes(model)&&row_obj['account']
    if (show_account) {
        row_obj['account']= permissions.accounts[row_obj.account]
    }
    return (
        <TableRow hover role="checkbox" aria-checked tabIndex={-1} key={index}>
            {cells.map((cell: string) => {
                let [path_cell, link, path] = ['', false, 'table'];

                if (is_isp&& model === NavigationEnum.ACCOUNTS_TABLE) {
                    if (cell.includes('.')) {
                        path_cell = cell.split('.')[1];
                        link = true;
                    } else if (cell === 'link_capacity') {
                        path = 'dashboard';
                        path_cell = NavigationEnum.NETWORKS_TABLE;
                        link = true;
                   
              
                       
                    }
                }
               
                return (
                    <TableCell sx={{ p: 0 }} align="center">
                        <Grid sx={{ display: 'flex', direction: 'row', justifyContent: 'space-evenly' }}>
                            {!['holistic', 'rate_limit'].includes(cell) ? (
                                !['type', 'apply_option'].includes(cell) ? (
                                    //    cell==='address'? pydict(row, cell, {ip:'22'}).ip:
                                    <Typography minWidth={20} alignContent={is_isp && link ?'left':"center"}>
                                        {pydict(row_obj, cell, ' ')}
                                    </Typography>
                                ) : (
                                    <FormattedMessage id={pydict(row_obj, cell, ' ')} />
                                )
                            ) : row_obj[cell] ? (
                                <matrieal_icons.CheckCircleIcon color="primary" />
                            ) : (
                                <matrieal_icons.DoNotDisturbOnIcon color="error" />
                            )}
                            {is_isp && link && (

                                <Link to={`/v/${row_obj['id']}/${path}/${path_cell}`} target="_blank">
                                    <matrieal_icons.ArrowCircleRightRoundedIcon color="secondary" sx={{ mb: -0.9  }} />
                                </Link>

                            )}
                        </Grid>
                    </TableCell>
                );
            })}

            {![NavigationEnum.ATTACKS_TABLE, NavigationEnum.FLOWS_TABLE, NavigationEnum.BLACKHOLE].includes(model) ? (
                <TableCell
                    align="center"
                    // style={{width:'100px', padding:'2px'}}
                    style={customColumnStyle}
                    sx={{ p: 0 }}
                >
                    <Link to={`${getPostsApi.get_prefix_to_navigate()}profile/${model}/${row_obj['id']}`}>
                        <IconButton color="primary" size="large">
                            <matrieal_icons.VisibilityTwoToneIcon sx={{ fontSize: '1.3rem' }} />
                        </IconButton>
                    </Link>
                    {permissions.can_edit_context_account && (
                        <>
                            <Link to={`${getPostsApi.get_prefix_to_navigate()}forms/${model}/${row_obj['id']}`}>
                                <IconButton color="secondary" size="large">
                                    {/* <Link to={`/v/${row_obj['id']}/table/${model}`}  target="_blank"> */}

                                    <matrieal_icons.EditTwoToneIcon
                                        sx={{ fontSize: '1.3rem' }}
                                        onClick={() => {
                                            getPostsApi.request_navigate(`forms/${model}/${row_obj['id']}`);

                                            // navigate(`/v/${row_obj['id']}/table/${model}`);
                                            // navigate(`/forms/${model}/${row_obj['id']}`);
                                        }}
                                    />
                                    {/* </Link> */}
                                </IconButton>
                            </Link>

                            <IconButton color="error" size="large">
                                {dialog_delete}
                                <matrieal_icons.DeleteForever
                                    sx={{ fontSize: '1.3rem' }}
                                    onClick={async () => {
                                        open_modal(row_obj);
                                    }}
                                />
                            </IconButton>
                        </>
                    )}
                </TableCell>
            ) : (
                <TableActionCell obj={row_obj} model={model} />
            )}
            {/* {dialog_delete} */}
        </TableRow>
    );
};

export default RowTable;
