import * as React from 'react';
import { FormattedMessage } from 'react-intl';
import { Grid,  Table, Button, TableBody, TableContainer } from '@mui/material';

import MainCard from 'ui-component/cards/MainCard';
import { EnhancedTableHead } from './TableHeader';
import { TableFlows } from '../table_flows/TableBase';
import TablePagination1 from '../TablePagination';
import useAuth from 'common/hooks/useAuth';

import { ACCOUNT_TYPE, ArrangementOrder, create_header_cell, DefaultRootStateProps, HeadCell, Language, NavigationEnum } from 'types';

import { useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';

import { get_table_data } from '..';
import { pydict } from 'utils/transformation/pydict';
import { useActions } from 'store/actions';
import DraggableDialog from 'ui-component/extended/DraggableDialog';
import config from 'config';
import useApi from 'common/hooks/useApi';
import RowTable from './RowTable';
import HocNavigate from 'common/hooks/useNavigate';


interface Props {
    cells: string[];
    model: NavigationEnum;
    headCells: HeadCell[];
    rowsPerPage: any;
    setRowsPerPage: any;
    update_table: Function;
}
const TableList = ({ cells, model, headCells, rowsPerPage, setRowsPerPage, update_table }: Props) => {
    const params = useParams();
    const { model_name } = params;
    const { delete_item } = useActions();

    const [order, setOrder] = React.useState<ArrangementOrder>('asc');
    const [orderBy, setOrderBy] = React.useState<string>('sort');
    const [page, setPage] = React.useState<number>(0);
    const hoc_navigate = HocNavigate();

    const [open, setOpen] = React.useState(false);

    const [dialog_delete, setDialog_delete] = React.useState<any>(null);

    const tableData = pydict(
        useSelector((state: DefaultRootStateProps) => state.models),
        model,
        {}
    );

    const [rows, setRows] = React.useState<any[]>(tableData.entities);

    React.useEffect(() => {
        setPage(0);
    }, [model_name]);

    React.useEffect(() => {
        setRows(tableData.entities);
    }, [model_name, tableData, tableData.entities]);
    React.useEffect(() => {
        !open && setDialog_delete(null);
    }, [open, rows]);
    const handleRequestSort = (event: React.SyntheticEvent<Element, Event>, property: string) => {
        const isAsc = orderBy === property && order === 'asc';
        setOrder(isAsc ? 'desc' : 'asc');
        setOrderBy(property);
    };

    // const isSelected = (name: string) => selected.indexOf(name) !== -1;
    // const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - rows.length) : 0;
    const add_model_button = (
        <Grid item mt={1}>
            <Button
                variant="contained"
                color="primary"
                sx={{ borderRadius: '1rem' }}
                onClick={() => {
                    hoc_navigate.request_navigate(`forms/${model}/add`);
                }}
            >
                <FormattedMessage id={Language.ADD} /> <FormattedMessage id={model} />
            </Button>
        </Grid>
    );
    const delete_itemApi = useApi(delete_item);

    const send_delete = async (object_id: string) => {
        const ans = await delete_itemApi.request(model, object_id);
        if (ans) {
            update_table();
        }
    };

    const open_modal = (row: any) => {
        setOpen(true);
        setDialog_delete(
            <DraggableDialog
                title={`Delete ${model.slice(0, -1).toLocaleUpperCase()}`}
                open
                func_send={send_delete}
                args={[row['id'], row]}
                setOpen={setOpen}
            />
        );
    };

    const { permissions } = useAuth();

    return (
        <MainCard headerSX={{ textAlign: 'center' }} title={model.toUpperCase().split('_').join(' ')} content={false}>
            <Grid container height={54} justifyContent="space-around">
                {permissions[`can_add_${model.slice(0, -1)}`] && add_model_button}
            </Grid>
            <TableContainer>
                <Table sx={{ minWidth: 750 }}>
                    <EnhancedTableHead
                        headers={headCells}
                        // numSelected={selected.length}
                        order={order}
                        orderBy={orderBy}
                        onRequestSort={handleRequestSort}
                        rowCount={rows ? rows.length : 0}
                        action
                    />
                    <TableBody>
                        {
                            rows &&
                                rows.map((row, index) => {
                                    if (typeof row === 'number') return null;

                                    return (
                                        <RowTable
                                            cells={cells}
                                            model={model}
                                            row_obj={row}
                                            index={index}
                                            dialog_delete={dialog_delete}
                                            open_modal={open_modal}
                                        />
                                    );
                                })
                        }
                    </TableBody>
                </Table>
            </TableContainer>

            <TablePagination1
                model={model}
                pagination_model
                rows={tableData.total}
                page={page}
                setPage={setPage}
                rowsPerPage={rowsPerPage}
                setRowsPerPage={setRowsPerPage}
            />
        </MainCard>
    );
};

const TableMain = () => {
    const params = useParams();
    const { model_name, page } = params as { model_name: NavigationEnum; page: string };
    const { set_models } = useActions();

    const { cells, headCells, body } = get_table_data(model_name);
    const { permissions } = useAuth();

    const [rowsPerPage, setRowsPerPage] = React.useState<number>(config.page_model);
    const getPostsApi = useApi(set_models);
    const update_table = () => {

        const bbody = { page_offset:page, page_limit: rowsPerPage, ...body };
        getPostsApi.request(model_name, bbody);
    };
    const savedCallback: any = React.useRef();

    React.useEffect(() => {
        savedCallback.current = update_table;
    }, [update_table]);

    React.useEffect(() => {
        
        const is_isp = [ACCOUNT_TYPE.ISP, ACCOUNT_TYPE.ROOT].includes(permissions.type_account) ;
        const show_account = is_isp&& [ NavigationEnum.NETWORKS_TABLE, NavigationEnum.DEVICES_TABLE, NavigationEnum.ACCOUNT_USERS_TABLE].includes(model_name)
        if (show_account &&! cells.includes('account') ) {
            
            cells.push('account')
            headCells.push(create_header_cell('account'))
        }
        savedCallback.current()
    }, [model_name, permissions]);
    // React.useEffect(() => {
    //     update_table();
    // }, [model_name]);

    const active_cooldown = ['cooldown', 'active'];
    return (
       Object.keys(permissions).length>0? <Grid>
            {!active_cooldown.includes(model_name) ? (
                    <MainCard>
                         <TableList
                                update_table={update_table}
                                cells={cells}
                                model={model_name as NavigationEnum}
                                headCells={headCells}
                                rowsPerPage={rowsPerPage}
                                setRowsPerPage={setRowsPerPage}
                            /> 
                    </MainCard>
            ) : (
                <TableFlows model={model_name} rowsPerPage={rowsPerPage} setRowsPerPage={setRowsPerPage} />
            )}
        </Grid>:null
 
    );
};

export default TableMain;
